:root {
  --color-black: #000;
  --color-white: #fff;
  --color-gray: #909090;
  --color-black-brown: #232323;
  --color-mandarin-tango: #e55047;
  --color-white-aluminum: #a5a5a5;
  --color-gainsborough: #dbdbdb;
  --color-smoky-white: #f9f9f9;
}

