ul {
  list-style: none;
}

img {
  max-width: 100%;
  vertical-align: top;
}

body {
  width: 100%;
  font-family: 'Roboto', sans-serif;
  background-color: var(--color-smoky-white);
}

._container {
  max-width: 980px;
  padding: 0 20px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.header {
  box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.5);
}

.header__top {
  height: 47px;
  background-color: #232323;
}

.header__top-container {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
}

.header__social-inner {
  display: flex;
  gap: 8px;
}

.header__social-link {
  padding-top: 3px;
  display: block;
  width: 25.5px;
  text-align: center;
  height: 47px;
  line-height: 47px;
}

.header__social-link:hover .header__social-icon {
  fill: var(--color-mandarin-tango);
}

.header__social-icon {
  fill: #fff;
}

.header__bottom {
  height: 56px;
  background-color: #fff;
}

.header__bottom-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.header__logo {
  display: flex;
  align-items: center;
  width: 120px;
  height: 56px;
  font-family: 'Bitter', serif;
  font-size: 24px;
  font-weight: 500;
  cursor: pointer;
}

.header__logo-i {
  width: 34px;
  height: 34px;
  line-height: 34px;
  color: #fff;
  text-align: center;
  background-color: var(--color-mandarin-tango);
}

.header__logo-text {
  margin-left: 9px;
  line-height: 16px;
  color: var(--color-black-brown);
}

.header__logo-text span {
  color: var(--color-mandarin-tango);
}

.menu-header {
  display: flex;
}

.nav__list {
  display: flex;
  gap: 27px;
  margin-top: 3px;
}

.nav__link {
  display: block;
  font-size: 12px;
  font-weight: 700;
  line-height: 56px;
  height: 56px;
  color: var(--color-black-brown);
  text-transform: uppercase;
}

.nav__link--active {
  color: var(--color-mandarin-tango);
}

.nav__link:hover {
  opacity: 0.6;
}